import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
// import Welcome from '@/views/welcome/index'
import Layout from '../views/layout/Layout'

const _import = require('./_import_' + process.env.NODE_ENV)  // 防止开发环境下热加载时编译时间过长

export const constantRouterMap = [
  { path: '/login', name: 'Login', hidden: true, component: _import('login/index') },
  { path: '/404', name: '404', hidden: true, component: _import('error/404') },
  { path: '/401', name: '401', hidden: true, component: _import('error/401') },
  { path: '/dashboard', name: 'Dashboard', redirect: '/welcome/index' },
  {
    path: '/',
    name: 'HelloWorld',
    hidden: true,
    component: HelloWorld
  },
  {
    path: '/welcome',
    name: 'Welcome',
    component: Layout,
    redirect: '/welcome/index',
    meta: { title: '二级标题示例', icon: 'eye' },
    children: [
      {
        path: 'index',
        name: 'Table',
        component: _import('welcome/index'),
        meta: { title: '列表', icon: 'table' }
      },
      {
        path: 'detail/:id',
        name: 'Tree',
        component: _import('welcome/detail'),
        meta: { title: '详情', icon: 'tree' }
      }
    ]
  },
  { path: '*', redirect: '/404' }
]
Vue.use(Router)

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  {
    path: '/example',
    name: 'Example',
    component: Layout,
    redirect: '/example/index',
    meta: { title: '权限示例', icon: 'eye', role: ['admin'] },
    children: [
      {
        path: 'index',
        name: 'EIndex',
        component: _import('example/index'),
        meta: { title: '列表', icon: 'table', role: ['admin'] }
      }
    ]
  }
]



