<template>
  <scroll-bar>
    <el-menu mode="vertical" unique-opened :default-active="$route.path" :collapse="isCollapse" background-color="#304156" text-color="#fff" active-text-color="#409EFF">
      <sidebar-item :routes="permission_routers" :testc="testCount"></sidebar-item>
    </el-menu>
  </scroll-bar>
</template>
<script>
import SidebarItem from './SidebarItem'
import ScrollBar from '@/components/ScrollBar'
import { mapGetters } from 'vuex'
export default {
  name: 'sidebar',
  data () {
    return {
      testCount: 123
    }
  },
  components: {
    SidebarItem,
    ScrollBar
  },
  computed: {
    ...mapGetters([
      'sidebar',
      'permission_routers'
    ]),
    routes () {
      const routes = this.$router.options.routes
      console.log('当前渲染出来的 router', routes)
      return routes
    },
    isCollapse () {
      const isColl = !this.sidebar.opened
      // console.log('sicoll', isColl, this.sidebar, this.$store)
      return isColl
    }
  }
}
</script>



