import router from './router'
import NProgress from 'nprogress' // 进度条动画
import 'nprogress/nprogress.css'// 进度条样式
import { getToken } from './utils/auth'
import store from './store' // 获取 vuex

// permissiom judge
function hasPermission (roles, permissionRoles) {
  if (roles.indexOf('admin') >= 0) return true // admin permission passed directly
  if (!permissionRoles) return true
  return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

const whiteList = ['/login', '/']
router.beforeEach((to, from, next) => {
    // to and from are both route objects
  NProgress.start()

  const token = getToken()
  console.log('获取 token ', token)
  // 新年快乐
  // 这里的验证 token 是存储在 cookie 中的, user 的模型里的 token 是发送远程时要用的
  if (getToken()) {
    console.log('已获取 token')
    if (store.getters.roles.length === 0) {
      console.log('此处应该更新用户信息')
      store.dispatch('GetInfo')
        .then((res) => {
          console.log('收到远程信息', store.getters.name, res)

          // 根据用户角色来过滤出合法的路由,并在回调中执行 addRoutes
          store.dispatch('GenerateRoutes', res.data).then(() => {
            console.log('vuex 中的路由', store.getters.permission_routers, store.getters.addRouters)
            router.addRoutes(store.getters.addRouters)
            next({...to, replace: true})
          })
        })
        .catch(() => {
          store.dispatch('FedLogOut').then(() => {
            console.log('验证失败,需要重新登录')
            next({path: '/login'})
          })
        })
    } else {
      if (hasPermission(store.getters.roles, to.meta.role)) {
        next()
      } else {
        next({path: '/401', query: { noGoBack: true }})
        NProgress.done() // router在hash模式下 手动改变hash 重定向回来 不会触发afterEach 暂时hack方案 ps：history模式下无问题，可删除该行！
      }
    }

    // next()
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
      NProgress.done()
    }
  }

  next()
})

router.afterEach(() => {
  NProgress.done()
})



