import { constantRouterMap, asyncRouterMap } from '@/router'

/**
 * 将对应权限的路由筛选出来
 * @param {array} asyncRouterMap  所有待判断权限的路由
 * @param {array} roles           所拥有的权限
 */
function filterAsyncRouter (asyncRouterMap, roles) {
  const accsssedRouters = asyncRouterMap.filter(route => {
    if (hasPermission(roles, route)) {
      if (route.children && route.children.length) {
        route.children = filterAsyncRouter(route.children, roles)
      }
      return true
    }
    return false
  })

  return accsssedRouters
}

/**
 * 判定单条路由是否符合用户所属的权限
 * @param {array} roles   用户所属权限
 * @param {obj} route     待判定的路由
 */
function hasPermission (roles, route) {
  if (route.meta && route.mate.role) {
    return roles.some(role => route.mate.role.indexOf(role) >= 0)
  } else {
    return true
  }
}

const permission = {
  state: {
    routers: constantRouterMap,
    addRouters: []
  },
  mutations: {
    SET_ROUTERS: (state, routers) => {
      state.addRouters = routers
      state.routers = constantRouterMap.concat(routers)
    }
  },
  actions: {
    GenerateRoutes ({commit}, data) {
      return new Promise(resolve => {
        const { roles } = data
        // console.log('角色', roles)
        let accessRouters
        if (roles.indexOf('admin' >= 0)) {
          accessRouters = asyncRouterMap
        } else {
          accessRouters = filterAsyncRouter(asyncRouterMap, roles)
        }
        console.log('过滤后的路由', accessRouters)
        commit('SET_ROUTERS', accessRouters)

        resolve()
      })
    }
  }
}
export default permission



